.Graph {
  /* margin: 40px; */
  /* width: 100%; */
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

}

.main-graph {
  background-color: rgb(233, 233, 120);
}
.main-graph .bar {
  fill: rgb(119, 224, 119);
}
.main-graph .bar:hover {
  fill: rgb(169, 169, 238);
}

#tooltip {
  background-color: rgb(82, 150, 206);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  text-align: center;
  width: 150px;
  height: 50px;
  padding: 2px;
  font: 12px;
  box-shadow: 1px 1px 10px;
  border-radius: 2px;
  pointer-events: none;
}
#tooltip.visible {
  opacity: 0.7;
}
#tooltip.invisible {
  opacity: 0;
}